@import url(https://fonts.googleapis.com/css?family=Montserrat|Open+Sans|Roboto);
* {
  box-sizing: border-box;
}

body {
  margin: 0;
  padding: 0;
  font-family: 'Montserrat', 'Roboto', sans-serif;
}

.Error-msgs{
  width: 70%;
  text-align: center;
  margin: 5rem auto;
  text-transform: capitalize; 
}
.main-header {
    width: 100%;
    height: 5rem;
    background: #ec7363;
}

.main-header__nav {
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    width: 100%;
    height: 100%;
    padding: 0 1rem;
}

.main-header__nav-items {
    height: 100%;
    list-style: none;
    margin: 0;
    padding: 0;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    /* margin-left: auto; */
}

.logo{
    width: 60%;
    border: 1px solid black; 
}

.main-header__nav-item {
    margin: 0 2rem;
}

.main-header__nav-item a {
    display: block;
    padding: 0.25rem 0.5rem;
    text-decoration: none;
    color: white;
}

.main-header__nav-item a:hover,
.main-header__nav-item a:active,
.main-header__nav-item a.active {
    background: #dd0c16;
    border-radius: 5px;
    color: white;
}

.main-header__nav-item button {
    font: inherit;
    cursor: pointer;
    border: none;
    background: transparent;
    padding: 0.25rem 0.5rem;
    color: white;
}

.main-header__nav-item button:hover,
.main-header__nav-item button:active {
    background: #dd0c16;
    border-radius: 5px;
    color: white;
}

.main-header__nav-item button:focus {
    outline: none;
}

.logo{
    width: 50%;
}
.button {
    font: inherit;
    border: 1px solid #c00707;
    background: #ee5c5c;
    color: white;

    cursor: pointer;
    padding: 0.5rem 1rem;
}

.button:focus {
    outline: none;
}

.button:hover,
.button:active {
    background: #c00707;
    color: white;
}
.modal {
    position: fixed;
    top: 30vh;
    left: 10%;
    width: 80%;
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.26);
    z-index: -1;
    background: white;
    border-radius: 3px;
    -webkit-transform: translateY(-3rem);
            transform: translateY(-3rem);
    opacity: 0;
    transition: all 0.3s ease-out;
}

.modal.open {
    -webkit-transform: translateY(0);
            transform: translateY(0);
    opacity: 1;
    z-index: 100;
}

.modal__header {
    padding: 1rem;
    border-bottom: 1px solid #6200ff;
}

.modal__header h1 {
    margin: 0;
    font-size: 1.25rem;
    color: #6200ff;
}

.modal__content {
    padding: 1rem;
}

.modal__actions {
    text-align: right;
    padding: 1rem;
}

@media (min-width: 768px) {
    .modal {
        left: 30%;
        width: 40%;
    }
}
.backdrop {
    position: fixed;
    top: 0;
    left: 0;
    height: 100vh;
    width: 100vw;
    z-index: -1;
    background: rgba(0, 0, 0, 0.6);
    opacity: 0;
    transition: opacity 0.1s ease-in;
}

.backdrop.show {
    z-index: 10;
    opacity: 1;
}
.product-item {
    display: -webkit-flex;
    display: flex;
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.26);
    height: 12.5rem;
    margin: 1rem 0;
    position: relative;
}

.product-item__image {
    width: 35%;
    height: 100%;
    background-position: center;
    background-size: cover;
    margin-right: 5%;
}

.product-item__content {
    width: 60%;
}

.product-item__content h1 {
    font-size: 1.25rem;
    color: #6200ff;
    margin: 0.5rem 0;
}

.product-item__content h2 {
    font-size: 1rem;
    color: #292929;
    margin: 0;
}

.product-item__controls {
    position: absolute;
    bottom: 1rem;
    right: 1rem;
    text-align: right;
}

.product-item__controls button,
.product-item__controls a {
    text-decoration: none;
    cursor: pointer;
    font: inherit;
    border: none;
    background: transparent;
    color: #6200ff;
    padding: 0;
    margin: 0 0.5rem;
}

.product-item__controls button:hover,
.product-item__controls button:active,
.product-item__controls a:hover,
.product-item__controls a:active {
    color: #320083;
}
.products {
    width: 95%;
    margin: auto;
}


@media (min-width: 768px) {
    .products {
        width: 80%;
    }
}
.product-page {
    text-align: center;
}

.product-page__image {
    width: 25rem;
    height: 25rem;
    background-position: center;
    background-size: cover;
    margin: auto;
}
.input {
  width: 100%;
  margin: 0.5rem 0;
}

.input input,
.input textarea {
  font: inherit;
  width: 100%;
  border: 1px solid #ccc;
  padding: 0.25rem 0.5rem;
  background: white;
}

.input input:focus,
.input textarea:focus {
  border-color: #6200ff;
  outline: none;
}
.edit-product__form {
    width: 90%;
    margin: 3rem auto;
    border: 1px solid rgb(15, 12, 12);
    padding: 1rem;
    padding-bottom: 3rem;
}

.edit-product__form h1{
    text-align: center;
    font-weight: 500;
}

@media (min-width: 768px) {
    .edit-product__form {
        width: 40rem;
        padding: 1rem 4rem;
        padding-bottom: 3rem;
    }
}
.auth-main{
    
    width: 90%;
    margin: 4rem auto;
    border: 1px solid rgb(19, 14, 14);
    padding: 1rem;
    padding-bottom: 3rem;
}

.auth-main::after {
    content: "";
    background-image: url(https://source.unsplash.com/1380x720/?Pizza);
    opacity: 0.5;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    position: absolute;
    z-index: -1;   
  }

.auth__form {
    width: 90%;
    margin: auto;
    font-weight: 900;
}

.auth__form h1 {
    text-align: center;
    text-transform: capitalize;
    font-weight: 900;
}

.Menu {
    text-align: center;
    margin: 1rem;
}

@media (min-width: 768px) {
    .auth-main{
        width: 90%;
        margin: 4rem auto;
        padding: 1rem;
        padding-bottom: 3rem;
    }
    
    .auth__form {
        width: 40rem;
    }
}
